.reviews {
  padding: 126px 0 120px;
  margin-top: 789px;

  h2 {
    text-align: center;
    margin: 0 0 37px;

    @include vp-1199 {
      margin-bottom: 41px;
    }

    @include vp-767 {
      margin-bottom: 43px;
    }
  }

  img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    object-fit: cover;

    @include vp-767 {
      width: 68px;
      height: 68px;
      margin: 0 0 0 12px;
    }
  }

  h3 {
    margin-bottom: 15px;
    font-size: 20px;
    line-height: 23px;

    @include vp-767 {
      position: absolute;
      top: 48px;
      right: 40px;
      width: 27.19%;
      line-height: 26px;
    }
  }

  p {
    line-height: 22px;
    margin: 0;

    @include vp-767 {
      text-align: center;
    }
  }

  @include vp-1199 {
    padding: 152px 0 100px;
  }

  @include vp-767 {
    padding: 87px 0 70px;
  }


  .swiper__button {
    @include vp-1199 {
      top: 17px;
    }

    @include vp-767 {
      top: 10px;
    }
  }

  .swiper-slide-reviews {
    display: flex;
    justify-content: space-between;
    background-color: $color-resolution-blue;
    padding: 59px 62px 57px;
    margin-top: 2px;

    @include vp-767 {
      position: relative;
      flex-direction: column;
      padding: 40px 29px 35px;
    }
  }

  .swiper-slide__info-reviews {
    width: 75.23%;
    position: static;
    padding: 0;
    color: $color-default-white;
    align-items: flex-start;

    @include vp-1199 {
      width: 73.87%;
    }

    @include vp-767 {
      width: 100%;
      margin-top: 16px;
    }
  }

  .swiper-button-disabled {
    opacity: 1;

    &::after {
      opacity: 0.35;
    }
  }
}

.swiper-reviews {
  width: 56.02vw;

  @include vp-1199 {
    width: 100vw;
  }
}
